@import './colors';

body {
  background-color: $color2;
  margin: 0;
  color: $colorB; }

section {
  height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid black; }

.payload {
  height: 100%;
  width: 100%;
  margin: 2.5em 1em;
  max-width: 120rem;
  vertical-align: center;
  background-color: $color5;
  display: flex;
  align-items: center; }

.headerPadding {
  transform: translatex(-.5em); }
